  getTotal2() {

    const tt = this.cart.reduce((i, j) => i + j.price * j.amount, 0);
    if (tt !== 0) {   this.Form.controls['total'].setValue(tt);
   } else {
      this.Form.controls['total'].setValue('');
    }

    var tt2=this.Form.controls['totalenvio'].value

    return Number(tt) + Number( tt2);
  }



  onSelectcategoria(selectedValue: any) {
    if (selectedValue.detail.value) {   let arr = (selectedValue.detail.value).split('');
        this.Form.controls['totalenvio'].setValue(arr[1]);
        this.totalenvio2=arr[1]
    }
    else
    {
      this.Form.controls['totalenvio'].setValue('0');
      this.totalenvio2='0'


    }


  }
  getTotal() {

    const tt = this.cart.reduce((i, j) => i + j.price * j.amount, 0);
    if (tt !== 0) {   this.Form.controls['total'].setValue(tt);
   } else {
      this.Form.controls['total'].setValue('');
    }

    return tt;
  }

