    async deleteRecord(item: any) {
     const alert = await this.alertController.create({
       header: this.seguro,
       message: '',
       buttons: [
         {
           text: 'No',
          cssClass: 'secondary',
           handler: (blah) => {
           }
         }, {
            text: 'Si',
            handler: () => {
              const recordID: string		= item._id;
              this.authenticationService.deleteReg(recordID +  '/' + this.userinfo[0].email + '/' + this.userinfo[0].idempresa
                + '/na' , 'productos').then((data) => {
                this.pideregistros( this.currentSegment);
                }, (err) => {
                  this.authenticationService.presentAlert(err.error, this.msg, '');
              });
            }
          }
        ]
      });
      await alert.present();
    }
    async verdetalle(item: any) {

      const modal = await this.modalController.create({
        component: MyModalcard2Page,
        animated: true,
        componentProps:  {moneda: this.moneda, data: item, correo: this.userinfo[0].correo , estadopedido: this.currentSegment  },

        presentingElement: await this.modalController.getTop() , cssClass: 'modal-fullscreen'
      });
      modal.onWillDismiss().then((data) => {
      });
      return await modal.present();

  }


  async openCart(datatt) {
    const modal = await this.modalController.create({
      component: MyModalcard3Page,
      animated: true,
      componentProps:  {datatt: datatt , moneda: this.moneda, },
      presentingElement: await this.modalController.getTop() , cssClass: 'modal-fullscreen'
    });
    modal.onWillDismiss().then((data) => {
          if (data.data.estado === 'exito') {
            this.otrasop2(datatt, 'Facturadas' , '7', 0 , data.data.idventa);
          }

    });
    return await modal.present();
  }



     loadData(event) {
      setTimeout(() => {
        event.target.complete();
        if ( this.items.length === 1000) {
          event.target.disabled = true;
        }
      }, 500);
    }

