
import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { AuthenticationService } from '../../services/Authentication.service';
import { AlertController , ModalController , NavParams} from '@ionic/angular';
import { Product } from '../../services/Authentication.service';
import { MyModalPage  } from '../my-modal/my-modal.page';
import { FormGroup, FormControl, Validators, FormGroupDirective } from '@angular/forms';


@Component({
  selector: 'app-my-modalcard2',
  templateUrl: './my-modalcard2.page.html',
  styleUrls: ['./my-modalcard2.page.scss'],
})
export class MyModalcard2Page implements OnInit {

  Form: FormGroup;
  cart: Product[] = [];
  variables: any = [];
  userinfo: any = [];
  nombre: any;
  moneda: any;
  estadopedido: any;
  estadoejecuta: any;
  totalenvio:any;
  totalproductos:any;
  grantotal:any;
  totalenvio2:any;
  botton1 = false;
  seguro = '';
  estadoorden:any;
  noorden:any;
  recibida:any;

  constructor(    public alertController: AlertController,  public translateService: TranslateService
    ,  private modalController: ModalController,   private navParams: NavParams
    , private authenticationService: AuthenticationService, private modalCtrl: ModalController) { }
  dismissModal() {
    this.modalCtrl.dismiss('close');
  }


  ngOnInit() {



    this.userinfo = this.authenticationService.getcopiauser();
    this.translateService.get('SEGUROM').subscribe((value) => {   this.seguro = value;  });

    this.cart = this.authenticationService.getCart();
    this.Form = new FormGroup({

       'direccion': new FormControl('', Validators.required),
       'nombre': new FormControl('', Validators.required),
       'telefono': new FormControl('', Validators.required),
       'tpago': new FormControl('', Validators.required),
       'total': new FormControl('', Validators.required),
       'direccion2a': new FormControl(''),
       'direccion2': new FormControl(''),
       'nombre2': new FormControl(''),
       'nit': new FormControl(''),
     
       'departamento': new FormControl('', Validators.required),
       'municipio': new FormControl('', Validators.required),
       'zona': new FormControl('', Validators.required),
       'pagaracambio': new FormControl('')

    });

    this.Form.controls['nombre'].setValue(this.navParams.data.data.nombre);
    this.Form.controls['direccion'].setValue(this.navParams.data.data.direccion);
    this.Form.controls['telefono'].setValue(this.navParams.data.data.telefono);
    this.Form.controls['tpago'].setValue(this.navParams.data.data.tipopago);
    this.Form.controls['total'].setValue(this.navParams.data.data.total);
    this.Form.controls['departamento'].setValue(this.navParams.data.data.departamento);
    this.Form.controls['municipio'].setValue(this.navParams.data.data.municipio);
    this.Form.controls['zona'].setValue(this.navParams.data.data.zona);
    this.Form.controls['direccion2a'].setValue(this.navParams.data.data.direccion2a);
this.totalenvio=this.navParams.data.data.totalenvio
this.totalproductos=this.navParams.data.data.totalproductos
this.grantotal=this.navParams.data.data.total
    this.Form.controls['pagaracambio'].setValue(this.navParams.data.data.pagaracambio);
    this.Form.controls['nombre2'].setValue(this.navParams.data.data.nombre2);
    this.Form.controls['direccion2'].setValue(this.navParams.data.data.direccion2);
    this.Form.controls['nit'].setValue(this.navParams.data.data.nit);
    

    this.moneda = this.navParams.data.moneda;
    this.recibida=this.navParams.data.data.createdAt
    this.noorden="1"
this.estadoorden=this.navParams.data.data.estado
    this.cart = this.navParams.data.data.dproductos;

    this.estadopedido = this.navParams.data.data.estado;
if ( this.userinfo[0].role !== 'Operador pedidos'  && this.userinfo[0].role !== 'Administrador') {
  this.botton1 = true;
} else {

  if (this.estadopedido === 'Abiertas') {      this.estadoejecuta = 'En proceso';    }
  if (this.estadopedido === 'En proceso') {      this.estadoejecuta = 'En camino';    }
  if (this.estadopedido === 'En camino') {      this.estadoejecuta = 'Entregadas';    }
  if (this.estadopedido === 'Entregadas') {  this.botton1 = true;    this.estadoejecuta = 'En proceso';    }

  if (this.estadopedido === 'Anuladas') {   this.botton1 = true;    this.estadoejecuta = 'En proceso';    }
  if (this.estadopedido === 'Abiertas') {  this.botton1 = true;     this.estadoejecuta = 'En proceso';    }

}


  }

  decreaseCartItem(product) {
    this.authenticationService.decreaseProduct(product);
  }

  increaseCartItem(product) {
    this.authenticationService.addProduct(product);
  }

  removeCartItem(product) {
    this.authenticationService.removeProduct(product);
  }



  close() {
    this.modalCtrl.dismiss();
  }


  async checkout() {

    const alert = await this.alertController.create({
      header: 'Gracias ' + this.Form.controls['nombre'].value ,
      message: 'El monto de sus compra es ' +  this.Form.controls['total'].value + ' <br>' + this.seguro,
      buttons: [
        {
          text: 'No',
         cssClass: 'secondary',
          handler: (blah) => {
          }
        }, {
           text: 'Si',
           handler: () => {



    const cad: any = [];
    for (let i = 0; i < this.cart.length; i++) {
      cad.push({
        idproveedor: this.cart[i].idproveedor,
        id: this.cart[i].id,
        name: this.cart[i].name,
        price: this.cart[i].price,
        amount: this.cart[i].amount,
        subtotal: (this.cart[i].price * this.cart[i].amount)
      });

    }

    const   options: any	     = {
      idempresa: this.userinfo[0].idempresa._id,
      nombre: this.Form.controls['nombre'].value,
      direccion  : this.Form.controls['direccion'].value,
      telefono  : this.Form.controls['telefono'].value,
      total  : this.Form.controls['total'].value,
      tipopago  : this.Form.controls['tpago'].value,

      idusuario  :  this.userinfo[0]._id,
      estado  : 'Abiertas',
      deliveryasignado  : '',
      dproductos  : cad,



      bitacora: { idempresa : this.userinfo[0].idempresa._id , idafiliado: '' ,
      email: this.userinfo[0].email , permiso : 'Crear', accion: 'Creacion de pedido de compra'}
};

  if (options) {
    this.authenticationService.createReg('', options, 'orden_compras').then((result) => {
      if (!this.Form.valid) { return; }
      this.authenticationService.presentAlert('Pedido creado con exito' , 'Gracias' , '');
      this.authenticationService.borrarProduct();
      this.modalCtrl.dismiss('close');
  }, (err) => {
    this.authenticationService.presentAlert(err.error , 'Precaucin' , '');
  });

  }

           }
         }
       ]
     });
     await alert.present();



  }
  async dacomponente(op, op2) {
    let uub = '';
    if ( op === 'ubicacion') {uub = this.Form.controls['ubicacion'].value; }
    const modal = await this.modalController.create({
      component: MyModalPage,
      swipeToClose: false,
      backdropDismiss: false,
     animated: true,
      presentingElement: await this.modalController.getTop(),
      componentProps:  { variables:  this.variables, record: null, userinfo: this.userinfo, uubx : uub}
      // , cssClass: 'modal-fullscreen'
    });
    modal.onWillDismiss().then((data) => {
         if ( data.data === 'close' ) {     }      else      {

// la  lo
         if (op === 'direccion') {
          this.Form.controls['direccion'].setValue(data.data.direccion);
         } else {
          this.Form.controls['ubicacion'].setValue(data.data.direccion + ' {' + data.data.la + '' + data.data.lo + '}');

         }



        }
    });
    return await modal.present();

  }
}
